.Characteristic{
    padding: 10px;
    padding-top: 20px;
    margin: 10px;
    min-width: 280px;
    max-width: 300px;
    display: inline-block;
    text-align: center;
    vertical-align: top;
    background-color: rgba(255, 255, 255, 0.75);
    -webkit-box-shadow: 5px 10px 8px #88888842;
            box-shadow: 5px 10px 8px #88888842;
    border-radius: 8px;
    font-weight: bold;
}

.Icon{
    height: 200px;
    border-radius: 50%;
}




